<template>
    <v-layout>

      
      <v-layout>
          <v-flex>
              <NavigationBar />
          </v-flex>
      </v-layout>
      

      <v-container class="my=5">
        <v-layout row class="ma-2 pa-2" wrap>
            <v-flex class="overflow mt-2" xs12 md8>

                <v-card class="mb-3" width="100%" height="30px"  color="warning">
                  <v-card-title class="ml-1 pa-0">Responsável pelo Uso</v-card-title>
                </v-card>
                
                <v-card class="mb-3" width="30%" height="30px"  color="black">
                  <v-card-title class="ml-1 pa-0">Pessoa Física</v-card-title>
                </v-card>

                <v-form ref="form" v-model="valid" :lazy-validation="lazy">

                    <v-text-field v-model="name" :counter="10" :rules="nameRules" label="Nome" required></v-text-field>
                    <v-text-field v-model="email" :rules="emailRules" label="E-mail" required></v-text-field>
                    <v-text-field v-model="cpf" :rules="cpfRules" label="CPF" required></v-text-field>
                    <v-text-field v-model="telefonePessoal" :rules="cpfRules" label="Telefone celular" required></v-text-field>

                </v-form>
                  
                <v-card class="mb-3" width="30%" height="30px"  color="black">
                  <v-card-title class="ml-1 pa-0">Pessoa Jurídica</v-card-title>
                </v-card>

                <v-form ref="form" v-model="valid" :lazy-validation="lazy">

                    <v-text-field v-model="cnpj" :counter="10" :rules="nameRules" label="CNPJ" required></v-text-field>
                    <v-text-field v-model="razaoSocial" :counter="10" :rules="nameRules" label="Razão Social" required></v-text-field>
                    <v-text-field v-model="nomeFantasia" :rules="emailRules" label="Nome Fantasia" required></v-text-field>
                    <v-text-field v-model="email" :rules="emailRules" label="E-mail da Empresa" required></v-text-field>
                    <v-text-field v-model="telefoneEmpresa" :rules="cpfRules" label="Telefone da Empresa" required></v-text-field>
                    <v-text-field v-model="nomeResponsavel" :rules="cpfRules" label="Nome Completo do Responsável pela Empresa" required></v-text-field>
                    <v-text-field v-model="cpfResponsavel" :rules="cpfRules" label="CPF do Responsável pela Empresa" required></v-text-field>

                </v-form>

                  <v-btn color="success" class="mr-4" @click="reset">
                      Salvar
                  </v-btn>



            </v-flex>
        </v-layout>
      </v-container>

      <v-spacer></v-spacer>

      <v-layout>
        <v-flex>
          <span></span>
        </v-flex>
      </v-layout>

    </v-layout>
</template>

<script>
import NavigationBar from '@/components/NavigationBar'

  export default {

    components: {
        NavigationBar
    },

    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4',
      ],
      checkbox: false,
      lazy: false,
    }),

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
        }
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },

  }
</script>

<style>

.separador {
  background-color: hotpink;
}

.overflow{
  overflow-y: auto;
}

</style>