<template>
  <div overflow-y="auto">
    <v-layout>

      
      <v-layout>
          <v-flex>
              <NavigationBar />
          </v-flex>
      </v-layout>
      

      <v-container class="my=5 separador">
        <v-layout row class="ma-2 pa-2" wrap>
            <v-flex class="mt-2" xs12 md8>

                <v-card class="mb-3" width="100%" height="30px"  color="warning">
                  <v-card-title class="ml-1 pa-0">Edificação</v-card-title>
                </v-card>

                <v-form ref="form" v-model="valid" :lazy-validation="lazy">

                    <v-text-field v-model="Rua" :rules="emailRules" label="Rua" required></v-text-field>
                    <v-text-field v-model="Numero" :rules="cpfRules" label="Nº" required></v-text-field>
                    <v-text-field v-model="Cep" :counter="10" :rules="nameRules" label="CEP" required></v-text-field>
                    <v-text-field v-model="Bairro" :rules="cpfRules" label="Bairro" required></v-text-field>
                    <v-text-field v-model="Município" :rules="cpfRules" label="Município" required></v-text-field>
                    <v-text-field v-model="Estado" :rules="cpfRules" label="Estado" required></v-text-field>

                </v-form>
                  
                <v-card class="mb-3" width="40%" height="30px"  color="black">
                  <v-card-title class="ml-1 pa-0">Dados da Edificação</v-card-title>
                </v-card>

                <v-form ref="form" v-model="valid" :lazy-validation="lazy">

                    <v-text-field v-model="AreaDoTerreno" :counter="10" :rules="nameRules" label="Área do Terreno" required>m²</v-text-field>
                    <v-text-field v-model="AreaConstruída" :counter="10" :rules="nameRules" label="Área Construída" required>m²</v-text-field>
                    <v-text-field v-model="AreaAnalisada" :counter="10" :rules="nameRules" label="Área a ser análisada" required>m²</v-text-field>
                    <v-text-field v-model="AlturaDescendente" :counter="10" :rules="nameRules" label="Altura Descendente(Entre pisos)" required>m²</v-text-field>
                    <v-text-field v-model="AlturaTotalEdificacao" :counter="10" :rules="nameRules" label="Altura Total da Edificacao" required>m</v-text-field>
                    <v-text-field v-model="NumeroDePavimentos" :counter="10" :rules="nameRules" label="N° de Pavimentos" required></v-text-field>
                    

                </v-form>

                  <v-btn color="success" class="mr-4" @click="reset">
                      Salvar
                  </v-btn>



            </v-flex>
        </v-layout>
      </v-container>

      <v-spacer></v-spacer>

      <v-layout>
        <v-flex>
          <span></span>
        </v-flex>
      </v-layout>

    </v-layout>
  </div>
</template>

<script>
import NavigationBar from '@/components/NavigationBar'

  export default {

    components: {
        NavigationBar
    },

    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4',
      ],
      checkbox: false,
      lazy: false,
    }),

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
        }
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },

  }
</script>

<style>

.separador {
  background-color: white;
}

</style>