<template>
    <v-toolbar dark>       

        <v-toolbar-title>Flame</v-toolbar-title>

        <v-spacer></v-spacer>    

        <v-btn color="red" text class="pa-2 mt-1 mr-4" @click="cadastro = true">
            Cadastre-se |
        </v-btn>

        <v-btn color="success" class="pa-2 mt-1" @click="login = true">
            Login
            <v-icon right>check_circle</v-icon>
        </v-btn>

        <v-dialog v-model="login" width="500">
            <v-card>
                <v-card-title>Faça seu Login</v-card-title>
                <v-card-text>Para entrar no site e cadastrar seus projetos:</v-card-text>
                <v-form class="pa-5">
                    <v-container>
                        <v-text-field v-model="usuarioLogando.email" label="E-mail" required></v-text-field>

                        <v-text-field v-model="usuarioLogando.senha" label="Senha" type="password" required></v-text-field>
                    </v-container>
                </v-form>
                <v-divider></v-divider>
                <v-layout justify-center>
                    <v-card-actions class="ml-2 pa-2">
                        <v-btn color="error" @click="login = false">Cancelar</v-btn>
                    </v-card-actions>
                    <v-card-actions class="ml-2 pa-2">
                        <v-btn color="success" @click="logar">Entrar</v-btn>
                    </v-card-actions>
                </v-layout>
            </v-card>
        </v-dialog>
        
        <v-dialog v-model="cadastro" width="500">
            <v-card>
                <v-card-title>Faça seu Cadastro</v-card-title>
                <v-card-text>Para poder construir seus projetos e utilizar os nossos serviços, por favor, faça seu cadastro:</v-card-text>
                <v-form class="pa-5">
                    <v-container>
                        <v-text-field v-model="usuario.nome" label="Nome Completo" required></v-text-field>

                        <v-text-field v-model="usuario.email" label="E-mail" required></v-text-field>

                        <v-text-field v-model="usuario.senha" label="Senha" type="password" required></v-text-field>
                    </v-container>
                </v-form>
                <v-divider></v-divider>
                <v-layout justify-center>
                    <v-card-actions class="ml-2 pa-2">
                        <v-btn color="error" @click="cadastro = false">Cancelar</v-btn>
                    </v-card-actions>
                    <v-card-actions class="ml-2 pa-2">
                        <v-btn color="success" @click="salvarUsuario">Cadastrar</v-btn>
                    </v-card-actions>
                </v-layout>
            </v-card>
        </v-dialog>


    </v-toolbar>
</template>

<script>
import HTTPutils from '@/Utils/HTTPutils'
import { stringify } from 'querystring';

export default {
    
    data: () => ({
        cadastro: false,
        login: false,
        
        usuario: {},
        usuarioLogando: {}
    }),

    methods: {
        salvarUsuario(){
            HTTPutils.salvarUsuario(this.usuario)
            .then ( usuarioNovo => {
                if( usuarioNovo._id != undefined){
                    console.log("Usuário salvo com sucesso")
                    this.usuario.nome = '',
                    this.usuario.email = '',
                    this.usuario.senha = ''
                } else {
                    console.log("Erro ao salvar Usuário")
                }
            })

            this.cadastro = false
        },

        logar() {
            HTTPutils.autenticar(this.usuarioLogando)
            .then ( usuarioRetornado => {
                
                if( usuarioRetornado != "Usuário não encontrado"){
                    alert("Login efetuado com sucesso!")
                    this.login = false
                    let usuarioString = JSON.stringify(usuarioRetornado)
                    this.login = false
                    this.$store.state.logado = true
                    
                } else {
                    alert("Não foi possível fazer o login, email ou senha inválidos, tente novamente.")
                    
                }
            })
        }

    }

};

</script>